'use strict';
$(document).ready(function(){


	$(".go-to").on("click",function(e){e.preventDefault();var t=$(this).attr("href");if($(t).length){var s=$(t).offset().top;$("body,html").stop().animate({scrollTop:s},1500)}else console.warn("ID don't search!")}),

	$('.header_contact_city').click(function(){
		$('.header_contact-cities').fadeIn(400).css('display' ,'flex').find('li').click(function(){
			$('.header_contact-cities').fadeOut();
		});
		$(document).mouseup(function (e) {
    var container = $(".header_contact-cities");
    if (container.has(e.target).length === 0){
        container.fadeOut();
    }
});
	}),

$('.education_slider').slick({
  slidesToShow: 1,
  slidesToScroll: 1,
  centerMode: true,
  prevArrow: $('.prev'),
nextArrow: $('.next')
}),


$(".video").click(function(){
	$(this).find(".video_link").fadeOut();
	$(this).find('iframe').fadeIn();

}),
$(document).on('click', '.video', function() {
	var $video = $(this).find('#video_frame'),
		src = $video.attr('src');
 
	$video.attr('src', src + '&autoplay=1', src + 'rel=0');
}),




$(".video_circle-wrapper").click(function(){
	$(this).closest(".video-callback_slide").fadeOut();
	$(this).fadeOut();
	$(this).next('iframe').fadeIn();

}),

$(document).on('click', '.video_circle-wrapper', function() {
	var $video = $(this).next('.video_frame'),
		src = $video.attr('src');
 
	$video.attr('src', src + '&autoplay=1', src + 'rel=0');
}),


$('.callback_slider').slick({
  slidesToShow: 1,
  slidesToScroll: 1,
  centerMode: true,
  prevArrow: $('.prevv'),
nextArrow: $('.nextt')
}),

$('.video-callback_slider').slick({
  slidesToShow: 1,
  slidesToScroll: 1,
  centerMode: true,
  prevArrow: $('.prevvv'),
nextArrow: $('.nexttt'),
dots: true
}),

$('.teachers_slider').slick({
  slidesToShow: 1,
  slidesToScroll: 1,
  centerMode: true,
  prevArrow: $('.prevvvv'),
nextArrow: $('.nextttt')
}),


$(".header_mobile-menu-wrap").click(function(){
	$(".header_nav-main").toggleClass("active_btn");
})



$('.prices_tab-content').hide();
$('.prices_tab-content:first').show();
$('.tabs li:first').addClass('active');

$('.tabs li').click(function(e){
	$('.tabs li').removeClass('active');
	$(this).addClass('active');
	$('.prices_tab-content').hide();

	var selectTab = $(this).find('a').attr("href");
	$(selectTab).fadeIn(500);

}),

$('.popup-with-zoom-anim').magnificPopup({
		type: 'inline',

		fixedContentPos: false,
		fixedBgPos: true,
		
		
		
		showCloseBtn: true,

		overflowY: 'auto',

		closeBtnInside: true,
		preloader: false,
		
		midClick: true,
		removalDelay: 300,
		mainClass: 'my-mfp-zoom-in'
	});


	
})